.contactItem {
  color: beige;
  margin-bottom: 20px;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}

.button {
  text-decoration: none;
  background: none;
  outline: none;
  margin: 10px 20px;
  position: relative;
  padding: 5px 20px;
  border: 1px solid #f1c40f;
  color: #f1c40f;
  transition: 0.4s;
}

.button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: auto;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  transition: 0.4s;
}

.button:hover::after {
  border-color: #f1c40f;
  width: calc(100% - 10px);
  height: calc(100% + 10px);
}
